$char-w: 1ch;
$gap: 0.5 * $char-w;
$n-char: 7;
$in-w: $n-char * ($char-w + $gap);

input {
  display: block;
  border: none;
  margin: 25px auto auto;
  padding: 0;
  width: $in-w;
  background: repeating-linear-gradient(90deg, dimgrey 0, dimgrey $char-w, transparent 0, transparent $char-w + $gap) 0
    100% / #{$in-w - $gap} 2px no-repeat;
  font: 3ch droid sans mono, consolas, monospace;
  letter-spacing: $gap;

  &:focus {
    outline: none;
    color: dodgerblue;
  }
}