.table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #cecece;

  thead {
    background-color: #000;
    height: 60px;
    color: #ffffff;

    th {
      text-align: center;
    }
    td {
      text-align: center;
    }
  }

  tbody tr {
    background-color: #ffffff;

    td {
      padding: 10px 0;
      border-bottom: 1px solid #cecece;
      text-align: center;
    }
    &:hover {
      background-color: #f8f7f7;
    }
  }
}
